// 查看 ETF 详情

'use client';

import { useParams } from 'next/navigation';
import { useState, useEffect } from 'react';
import { Spin, Card } from 'antd';
import ContentHeader from '@/components/ContentHeader';
import ETFChart from '@/components/ETFChart';

interface ETFData {
  id: number;
  code: string;
  name: string | null;
  category: string;
}

interface PriceData {
  date: string;
  value: number;
}

const ETFDetailPage = () => {
  const { id } = useParams();
  const [etf, setETF] = useState<ETFData | null>(null);
  const [priceData, setPriceData] = useState<PriceData[]>([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    if (!id) return;

    const fetchETFDetail = async () => {
      try {
        const data = await fetch(`/api/pool/${id}`).then(res => res.json());
        setETF(data);
      } catch (error) {
        console.error('Error fetching ETF detail:', error);
      } finally {
        setLoading(false);
      }
    };

    fetchETFDetail();
  }, [id]);

  useEffect(() => {
    if (!etf?.code) return;

    const fetchPriceData = async (code: string) => {
      try {
        const data = await fetch(`/api/pool/price?code=${code}`).then(res =>
          res.json()
        );
        setPriceData(data);
      } catch (error) {
        console.error('Error fetching price data:', error);
      }
    };

    fetchPriceData(etf.code);
  }, [etf]);

  if (loading) {
    return (
      <div className="flex justify-center items-center h-64">
        <Spin size="large" />
      </div>
    );
  }

  if (!etf) {
    return <div className="text-center text-gray-500 mt-8">ETF 未找到</div>;
  }

  return (
    <div>
      <div className="mb-6">
        <ContentHeader
          title={etf.name || etf.code}
          description={`代码: ${etf.code} | 类别: ${etf.category}`}
          action={<div />}
        />
      </div>
      <Card className="bg-white rounded-lg shadow-card">
        <ETFChart
          data={priceData}
          title={`${etf.name || etf.code} 日线趋势图`}
        />
      </Card>
    </div>
  );
};

export default ETFDetailPage;
